import { SurveyContext } from '../../../utils/context';
import { useContext } from 'react';
import IsEStore from '../IsEStore';
import { TitleH2 } from '../../../utils/style/Atoms';

function Craftman() {
    const { repliesId } = useContext(SurveyContext);

    function offerType() {
        if (repliesId.includes('1c') || repliesId.includes('1b')) {
            return "produit"
        }
        if (repliesId.includes('1d')) {
            return "service"
        }
        if (repliesId.includes('1e')) {
            return "oeuvre"
        }
    }

    if (repliesId.includes('2a')) {
        return <div>
            <p>Votre site internet sera avant tout une vitrine virtuelle qui permettra de prsenter vos {offerType()}s  vos futurs clients. Ils pourront ainsi visualiser votre travail, vos comptences et, le cas chant, votre style.</p>
            <p>En dtaillant les processus inhrents  votre savoir-faire, nous ferons comprendre aux clients potentiels que choisir un artisan, cest privilgier la qualit. Nous pourrons inclure dans votre site des tmoignages et des avis client ce qui renforcera votre crdibilit et votre professionnalisme tout en insistant sur votre capacit  vous adapter aux diffrentes problmatiques que vous rencontrez. Cela permettra galement {repliesId.includes('5b') && <span>de mettre l'accent sur votre crativit, votre adaptabilit et</span>} un meilleur <dfn title='Le rfrencement correspond  votre position sur les moteurs de recherche quand le visiteur tape certains mots-cls.'>rfrencement</dfn> dans votre domaine dexpertise.</p>
            {!(repliesId.includes('1b')) && <div>
                <TitleH2>Un design adapt</TitleH2>
                <p>De plus, pour faire connaitre vos {offerType()}s dans votre rgion, nous mettrons aussi en place un rfrencement dordre gographique {repliesId.includes('1f') && <span>et une fonctionnalit permettant de publier des atualits sur vos vnements</span>}. Nous dfinirons ainsi un ensemble de mots-cls adapts  votre public cible. Il sera important que ces mots-cls apparaissent rgulirement sur votre site{repliesId.includes('1f') && <span> et sur vos publications</span>}.</p>
                
                <TitleH2>Un design sur-mesure</TitleH2>
                <p>Par ailleurs, nous adapterons le design de votre site  vos {offerType()}s.
                    {repliesId.includes('4b') ? <span>Dans votre cas, comme vos {offerType()}s s'adressent  des professionnels, il faudra mettre l'accent sur la ractivit et la fiabilit. Pour cela, il sera ncessaire de choisir un design sobre et classique dans lequel vos prospects auront aisment accs aux informations essentiels.</span>
                        : <span>Pour des {offerType()}s uniques par exemple, nous choisirons un design sobre et minimaliste. Cela accentuera la spcificit de chaque demande pour vos futurs clients.</span>}{repliesId.includes('5b') && <span> Pour mettre l'accent sur la crativit, nous choisirons quelques couleurs vives  intgrer au design. Elles sont gnralement associes  l'imaginaire.</span>}</p>
                <p>Enfin, votre site internet permettra  ses visiteurs de faire des demandes de devis adapt  votre savoir-faire{repliesId.includes('1g') && <span>, prendre des rendez-vous sur des plages horaires de votre choix</span>} et de trouver facilement vos informations de contact.</p>
            </div>}
            <IsEStore />
        </div>
    }
}

export default Craftman